.app-header {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
        align-items: center;
    height: 60px;
    position: relative;
    z-index: 10;
    /*background-color: white;*/
    /*margin-bottom: 10px;*/
}

/*.vl {*/
/*    border: 0.5px solid;*/
/*    height: 30px;*/
/*    max-width: 0;*/
/*}*/

.app-title {
    line-height: 60px;
    vertical-align: middle;
    font-size: 18px;
    font-weight: 500;
    /*color: #152944;*/
    color: white;
}
/*.app-title:hover {*/
/*    color: #4b4f56;*/
/*}*/

.app-branding {
    /*float: left;*/
    display: -ms-flexbox;
    display: flex;
    -ms-flex-flow: row nowrap;
        flex-flow: row nowrap;
    -ms-flex-align: center;
        align-items: center;
}

.app-options {
    /*float: right;*/
}

.app-nav {
    float: right;
}

.app-nav ul {
    list-style-position: none;
    margin: 0;
    padding: 0;
    font-size: 16px;
}

.app-nav ul li {
    list-style-type: none;
    display: inline-block;
    font-weight: 500;
    /*border-right: 1px solid rgba(0, 0, 0, 0.1);*/
    /*margin-left: 5px;*/
    /*margin-right: 5px;*/
}

.app-nav ul li a {
    display: inline-block;
    line-height: 60px;
    vertical-align: middle;
    padding: 0 15px;
    /*padding-right: 15px;*/
    color: white;
    font-weight: 500;
}

.app-nav ul li a:hover {
    /*color: #4b4f56;*/
    background-color: #3B70C5;
    height: 40px;
    line-height: 40px;
    border-radius: 5px;
}

.app-nav ul li a.active {
    border-bottom: 2px solid white;
}

.search {
    position: relative;
    color: #aaa;
    font-size: 16px;
    margin-left: 20px;
}

.search input {
    width: 350px;
    height: 32px;
    /*background: #fcfcfc;*/
    border: none;
    border-radius: 5px;
    /*box-shadow: 0 0 3px #ccc, 0 10px 15px #ebebeb inset;*/
}

.search input { text-indent: 32px;}
.search .fa-search {
    position: absolute;
    top: 10px;
    left: 10px;
}

.btn-profile {
    width: 32px;
    height: 32px;
    border-radius: 25px;
}
.icon-profile {
    width: 32px;
    height: 32px;
}


